"use client";
import { useTranslation } from "react-i18next";

// React & Next Imports
import { useEffect, useState } from "react";
//Redux 
import { useSelector } from "react-redux";
//Component
import Contactus from "../Contactus";


export default function PageDetail({ token, pagedetail }) {
  const { t } = useTranslation();
  
  const [loader, setLoader] = useState(false);
  const [pageData, setPageData] = useState();
  const [triger, setTriger] = useState(0);
  const grouppage = useSelector((state) => state.userReducer.pagegroup);

  useEffect(() => {
    if (pagedetail) {
      setPageData(pagedetail);
      setLoader(true);
      setTriger(triger + 1);
    }
  }, [pagedetail]);

  useEffect(() => {
    if (triger != 0) {
      setLoader(false);
    }
  }, [grouppage]);

  const decodeHtmlEntities = (str) => {
    const txt = document.createElement("textarea");
    txt.innerHTML = str;
    return txt.value;
  };
  const description = decodeHtmlEntities(
    pageData
      ? pageData?.[0]?.pageTranslation?.content
        ? pageData?.[0]?.pageTranslation?.content
        : pageData?.[0]?.content
      : ""
  );

  return (
    <>
      <main className="p-4 mx-auto mt-1 bg-white sm:p-10 max-w-screen-2xl">
        <div className="flex sm:flex-row flex-col items-start sm:items-center gap-4 sm:gap-[92px] mb-10 pb-10 border-grey-100 border-b">
          {loader == false ? (
            <div role="status" className="w-full animate-pulse">
              <div className="bg-gray-200 dark:bg-gray-700 mb-4 rounded-full w-1/4 h-3.5"></div>
              <div className="w-full h-3 mb-3 bg-gray-200 rounded-full dark:bg-gray-700"></div>
              <div className="h-3 mb-3 bg-gray-200 rounded-full dark:bg-gray-700"></div>
              <div className="w-full h-3 mb-3 bg-gray-200 rounded-full dark:bg-gray-700"></div>
              <div className="w-full h-3 mb-3 bg-gray-200 rounded-full dark:bg-gray-700"></div>
              <div className="w-full h-3 mb-3 bg-gray-200 rounded-full dark:bg-gray-700"></div>
              <div className="w-1/3 h-3 bg-gray-200 rounded-full dark:bg-gray-700"></div>
              <span className="sr-only">{t("productcontent.loading")}</span>
            </div>
          ) : (
            <div className="flex flex-col space-y-5">
              <h2 className="font-black text-[42px] text-black-300 leading-[50px]">
                {pageData
                  ? pageData?.[0]?.pageTranslation?.title
                    ? pageData?.[0]?.pageTranslation?.title
                    : pageData?.[0]?.title
                  : ""}
              </h2>
              {/* <p
                className="text-xl font-normal leading-7 text-black-300 list-default bullet"
                dangerouslySetInnerHTML={{
                  __html: pageData ? pageData?.[0]?.pageTranslation?.content ? pageData?.[0]?.pageTranslation?.content : pageData?.[0]?.content : ''
                }}
              /> */}

              {/* Style backup */}
              {/* className="text-xl font-normal leading-7 text-black-300 list-default bullet" */}
              <div
                className="text-black-300 list-default bullet editor-style"
                dangerouslySetInnerHTML={{
                  __html: description
                    ?.replaceAll("&amp;", "&")
                    .replaceAll("&lt;", "<")
                    .replaceAll("&gt;", ">")
                    .replaceAll("&quot;", '"')
                    .replaceAll("&#39;", "'")
                    .replaceAll("&sbquo;", "‚")
                    .replaceAll("&#61;", "=")
                    .replaceAll("&#45;", "-")
                    .replaceAll("&hellip;", "…")
                    .replaceAll("&commat;", "@")
                    .replaceAll("&copy;", "©")
                    .replaceAll("&#35;", "#")
                    .replaceAll("&ldquo;", "“")
                    .replaceAll("&rsquo;", "’")
                    .replaceAll("&lsquo;", "‘")
                    .replaceAll("&trade;", "™")
                    .replaceAll("&reg;", "®")
                    .replaceAll("&ndash;", "–")
                    .replaceAll("&eacute;", "é")
                    .replaceAll("&euro;", "€")
                    .replaceAll("&pound;", "£")
                    .replace(
                      "&lt;/p&gt;&amp;&#35;10;&amp;&#35;10;&lt;p&gt;",
                      ""
                    ),
                }}
              />
            </div>
          )}
        </div>
      </main>

      <Contactus token={token} />
    </>
  );
}
